import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators, FormGroup, FormControl } from '@angular/forms';

import { MdDialog } from '@angular/material';

import { GlobalValidator } from '../../../validators/validators';

import { CompanyService } from "../../company/company.service";
import { UserService } from "../../user/user.service";

@Component({
  selector: 'app-user-form',
  templateUrl: './user-form.component.html',
  styleUrls: ['./user-form.component.css']
})
export class UserFormComponent implements OnInit {

  public title = "Nuevo usuario";

  private busy: boolean;
  public set Busy(value: boolean) {
    if (value) {
      this.userForm.disable();
    }
    else {
      this.userForm.enable();
    }
    this.busy = value;
  }
  public get Busy(): boolean { return this.busy; }

  private submitted: boolean = false;

  public errorMessages: {};

  public controls = {
    first_name: new FormControl("", Validators.required),
    last_name: new FormControl("", Validators.required),
    email: new FormControl("", Validators.compose([Validators.required, GlobalValidator.mailFormat])),
    type: new FormControl("", Validators.required),
    company_key: new FormControl(""),
    group_key: new FormControl(""),
    hotel_key: new FormControl(""),
  }
  public userForm: FormGroup = this.fb.group(this.controls);

  constructor(
    private dialog: MdDialog,
    private fb: FormBuilder,
    private companyService: CompanyService,
    private userService: UserService,
  ) {
    this.Busy = true;

    let promises = [];
    promises.push(this.companyService.getMap().then((companyMap) => { this.companyMap = companyMap; }));
    promises.push(this.userService.getAllowedRolesList().then((allowedRoles) => { this.allowedRoles = allowedRoles; }));

    Promise.all(promises).then((result) => this.Busy = false);
  }

  ngOnInit() {
  }

  public save() {
    if (this.Busy) return;
    this.markAsTouched(true);
    this.userForm.updateValueAndValidity();
    if (this.userForm.invalid) return;
    this.Busy = true;


    this.submitted = true;

    console.log("save");
    this.userService.newUserR()

  }

  public cancel() {
    this.dialog.closeAll();
  }

  private handleError() {

  }

  public allowedRoles = [];


  public companyMap: any = {};

  public typeChange() {
    for (let controlName in this.controls) {
      if (this.controls.hasOwnProperty(controlName)) {
        let required = this.userRequires[this.controls.type.value][controlName];
        if (required != undefined) {
          if (required)
            this.controls[controlName].setValidators(Validators.required);
          else
            this.controls[controlName].clearValidators();
        }
      }
    }

    if (this.controls.type.value == "HG_admin") {
      this.controls.company_key.reset("");
      this.company_keyChange();
    }

    if (this.controls.type.value == "CO_admin") {
      this.controls.group_key.reset("");
      this.group_keyChange();
    }

    this.userForm.updateValueAndValidity();
  }
  public company_keyChange() {
    this.controls.group_key.reset("");
    this.group_keyChange();
  }
  public group_keyChange() {
    this.controls.hotel_key.reset("");
  }

  private userRequires = {
    HG_admin: {
      company_key: false,
      group_key: false,
      hotel_key: false
    },
    CO_admin: {
      company_key: true,
      group_key: false,
      hotel_key: false
    },
    CO_manager: {
      company_key: true,
      group_key: true,
      hotel_key: true
    },
    CO_concierge: {
      company_key: true,
      group_key: true,
      hotel_key: true
    }
  }


  public showControl(controlName: string) {
    if (this.controls.type.value == "") return false;
    return this.userRequires[this.controls.type.value][controlName];
  }

  private markAsTouched(b: boolean) {
    for (let controlKey in this.controls) {
      if (this.controls.hasOwnProperty(controlKey)) {
        let control = this.controls[controlKey];
        control.markAsTouched(b)
      }
    }
  }

}


